.videoFrame {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  border: 10px solid #1d1d1d;
  background: #1d1d1d;
}
.videoFrame:before,
.videoFrame:after {
  content: '';
  display: block;
  position: absolute;
  height: 10px;
  background: #006dfe;
}
.videoFrame:before {
  width: 44px;
  bottom: 100%;
  left: 50%;
}
.videoFrame:after {
  width: 52px;
  top: 100%;
  right: 70px;
}
.videoFrame:hover .videoFrame__play {
  background: #1d1d1d;
}
.videoFrame:hover .videoFrame__play:before {
  background: #006dfe;
}
