  border-width: 20px;
}
.videoFrame--bordered:before,
.videoFrame--bordered:after {
  height: 20px;
}
.videoFrame--gray {
  border-color: #e9e9e9;
}
.videoFrame--gray:after {
  width: 29px;
  top: 100%;
  left: 147px;
}
.videoFrame--gray .videoFrame__play {
  bottom: -10px;
  right: -10px;
}
.videoFrame--gray .videoFrame__play:before {
  height: 10px;
}
.videoFrame__preview {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: 50% 50%;
}
.videoFrame__preview:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0,109,254,0) 70%, rgba(0,109,254,0.4) 100%);
}
.videoFrame__play {
  position: absolute;
  width: 193px;
  height: 94px;
  background: #006dfe;
  bottom: 0;
  right: 0;
  z-index: 1;
  transition: all 0.1s ease-in;
}
.videoFrame__play:before,
.videoFrame__play:after {
  content: '';
  display: block;
  position: absolute;
}
.videoFrame__play:before {
  width: 41px;
  height: 9px;
  background: #1d1d1d;
  left: 0;
  bottom: 0;
  transition: all 0.1s ease-in;
}
