.input {
  position: relative;
}
.input:before {
  content: '';
  display: block;
  position: absolute;
  width: 22px;
  height: 4px;
  background: #006dfe;
  top: 100%;
  margin-top: -1px;
}
.input__input {
  display: block;
  width: 100%;
  border-bottom: 1px solid #1d1d1d;
  font-size: 16px;
  color: #1d1d1d;
  padding: 10px 0;
}
.input__input--textarea {
  min-width: 100%;
  max-width: 100%;
}
.card {
  position: relative;
  width: 362px;
  border: 15px solid #e9e9e9;
  border-bottom: 0;
  background: #e9e9e9;
  transition: all 0.2s ease-in;
}
.card:before,
.card:after {
  content: '';
  position: absolute;
  width: 16px;
  height: 15px;
  background: #e9e9e9;
  bottom: 0;
  transition: all 0.2s ease-in;
  z-index: 1;
}
.card:before {
  left: 0;
}
.card:after {
  right: 0;
}
.card:hover {
  border-color: #006dfe;
}
.card:hover:before,
.card:hover:after {
  background: #006dfe;
}
.card:hover .card__inner:after {
  opacity: 0;
}
.card:hover .card__over {
  transform: none;
}
@media (max-width: 1199px) {
  .card {
    border-color: #006dfe;
  }
  .card:before,
  .card:after {
    background: #006dfe;
  }
  .card .card__inner:after {
    opacity: 0;
  }
  .card .card__over {
    transform: none;
  }
}
.card__inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.card__inner:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0,109,254,0) 40%, rgba(0,109,254,0.7) 100%);
  transition: all 0.2s ease-in;
}
.card__image {
  transition: all 0.1s ease-in;
}
.card__name {
  position: absolute;
  background: #fff;
  left: 26px;
  right: 26px;
  top: 100%;
  margin-top: -44px;
  height: 134px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 24px;
  line-height: 30px;
  box-shadow: 0 0 25px 0 rgba(29,29,29,0.1);
}
.card__name:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 16px;
  height: 16px;
  background: #006dfe;
}
.card__over {
  position: absolute;
  background: rgba(0,109,254,0.9);
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  padding: 20px 0;
  transition: all 0.2s ease-in;
  transform: translateY(-100%);
  z-index: 2;
}
.card__over-item {
  text-align: center;
  color: #fff;
  font-size: 16px;
  line-height: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card__number {
  border: 1px dashed #1d1d1d;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  min-width: 77px;
  padding: 4px 15px;
  margin: 13px auto 0;
}
.card__icon {
  margin-bottom: 12px;
}
.card__icon svg {
  fill: #fff;
  display: block;
  margin: 0 auto;
}
