  width: 100%;
  height: 100%;
  background: url("images/mountains.jpg") 50% 100% repeat-x, #fff;
  will-change: transform;
  transition: all 0.2s ease-in;
}
.menu--active {
  visibility: visible;
  opacity: 1;
  filter: none;
  transform: none;
}
